import { useEffect, useLayoutEffect, useState } from "react";

import { BlngTBody } from "./tableBody/BlngTBody";
import { HoTBody } from "./tableBody/HoTBody";
import { OffshoreTBody } from "./tableBody/OffshoreTBody";
import { OrmcTBody } from "./tableBody/OrmcTBody";
import { SbwTBody } from "./tableBody/SbwTBody";
import { ExportTableToExcel } from "./customTimeSheet/DownloadTableToExcel";
import "../../../src/index.css";
import { useNavigate } from "react-router-dom";

import { useTableFieldData } from "./customTimeSheet/UseTableFieldData";

import { useTempID } from "../../utils/TempIDContext";
import { ViewTimeSheet } from "./ViewTimeSheet";
import { Pagination } from "./timeSheetSearch/Pagination";

export const VTimeSheetTable = (
  {
    // AllFieldData,
    // categoryFilters,
    // data,
    // handleScroll,
  }
) => {
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState("");
  // const [tableData, setTableData] = useState(null);
  const [data, setData] = useState(null);
  const [secondaryData, setSecondaryData] = useState([]);

  // const [categoryFilters, setCategoryFilters] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const savedData = localStorage.getItem("timeSheetData");
  // console.log(savedData)
  const {
    showListTimeSheet,
    timeSheetFileData,
    startDate,
    endDate,
    searchQuery,
    setCategoryFilters,
    categoryFilters,
    setTableData,
    tableData,
  } = useTempID();
  const nav = useNavigate();
  // console.log(typeof savedData === "object", Array.isArray(savedData));
  // const [startDate, setStartDate] = useState("");
  // const [endDate, setEndDate] = useState("");
  useLayoutEffect(() => {
    if (showListTimeSheet === true) {
      const currentPath = window.location.pathname;
      if (
        currentPath === "/viewTsheetDetails" ||
        currentPath === "/viewTsheetDetails/"
      ) {
        nav("/viewTimesheet");
      }
    }
  }, []);

  useEffect(() => {
    if (timeSheetFileData) {
      localStorage.setItem("timeSheetData", timeSheetFileData?.updatedAt);
      // console.log(savedData?.updatedAt)
    }
  }, [timeSheetFileData]);

  const AllFieldData = useTableFieldData(categoryFilters);

  const safeData = data || [];
  const itemsPerPage = 10;
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentData = safeData.slice(indexOfFirstItem, indexOfLastItem);

  const totalPages = Math.ceil(safeData.length / itemsPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);
  let visibleData = currentData;

  useEffect(() => {
    if (secondaryData && secondaryData.length > 0) {
      setCurrentPage(1);

      setLoading(false);

      let filteredData = [...secondaryData];

      // Filter by search query
      if (searchQuery) {
        filteredData = searchQuery;
      }

      if (startDate && endDate) {
        const start = new Date(startDate); // Start date as "MM/DD/YYYY"
        const end = new Date(endDate); // End date as "MM/DD/YYYY"

        // Filter the data array
        filteredData = filteredData.filter((item) => {
          const itemDate = new Date(item.date); // Convert item.DATE to a Date object
          itemDate?.setHours(0, 0, 0, 0);
          start?.setHours(0, 0, 0, 0);
          end?.setHours(0, 0, 0, 0);
          return itemDate >= start && itemDate <= end;
        });
      }

      // Final filtered data handling
      setData(filteredData);

      setLoading(false);
    }
  }, [secondaryData, startDate, endDate, searchQuery]);

  useEffect(() => {
    if (!timeSheetFileData) return;
    if (timeSheetFileData) {
      setData(timeSheetFileData?.updatedAt);
      setSecondaryData(timeSheetFileData?.updatedAt);
      // setData(fileData?.updatedAt);
      // setSecondaryData(fileData?.updatedAt);
      setCategoryFilters(timeSheetFileData?.fileType);
    }
  }, [timeSheetFileData]);

  useEffect(() => {
    if (data) {
      setTimeout(() => {
        // setMessage("Please wait few seconds.");
        setLoading(false);
      }, 3000);
    } else {
      setTimeout(() => {
        // setMessage("No Table Data Available Here.");
        setLoading(false);
      }, 3000);
    }
  }, [data]);

  return (
    <section className="h-screen bg-[#fafaf6] ">
      {/* <header className="flex justify-between"> */}

      <ViewTimeSheet />

      {/* </header> */}
      <div className="screen-size top-0 ">
        <div className="table-container ">
          <table className="styled-table  w-[100%]">
            <thead className="sticky-header">
              <tr className="text_size_7">
                {categoryFilters !== "HO" && (
                  <td className="px-5 text-center">S No.</td>
                )}

                {AllFieldData?.tableHeader?.map((header, index) => (
                  <td key={index} className="px-4 flex-1 text-center">
                    {header}
                  </td>
                )) ?? (
                  <td colSpan="100%" className="text-center">
                    No headers available
                  </td>
                )}
              </tr>
            </thead>
            {categoryFilters === "BLNG" && (
              <BlngTBody
                data={visibleData}
                loading={loading}
                setTableData={setTableData}
                message={message}
              />
            )}
            {categoryFilters === "HO" && (
              <HoTBody
                data={visibleData}
                loading={loading}
                setTableData={setTableData}
                message={message}
              />
            )}
            {categoryFilters === "SBW" && (
              <SbwTBody
                data={visibleData}
                loading={loading}
                setTableData={setTableData}
                message={message}
              />
            )}
            {categoryFilters === "ORMC" && (
              <OrmcTBody
                data={visibleData}
                loading={loading}
                setTableData={setTableData}
                message={message}
              />
            )}
            {categoryFilters === "Offshore" && (
              <OffshoreTBody
                data={visibleData}
                loading={loading}
                setTableData={setTableData}
                message={message}
              />
            )}
          </table>
        </div>
      </div>
      {/* </div> */}

      <div className="flex justify-center py-7">
        {visibleData.length > 0 && (
          <Pagination
            totalPages={totalPages}
            currentPage={currentPage}
            paginate={paginate}
          />
        )}
      </div>
    </section>
  );
};
